import React, { Component } from 'react';
import Note from './Note';
import { connect } from "react-redux";
import axios from 'axios';
// import axios from "axios";

let mapNoteStateToProps = state => {
  return {
    notes: state.notes,
  }
};
let mapDispatchToProps = dispatch => {
  const header = new Headers({ 'Content-Type': 'application/json' });
  return {
    viewAllNotes: () => {
      dispatch({ type: "view_notes_pending" });
      axios.get("http://localhost:8080/notes/",
        { header })
        .then(response => {
          console.log('resonse', response);
          dispatch({ type: "view_notes_fulfilled", payload: response.notes });
        })
        .catch((response) => {
          dispatch({ type: 'view_notes_failed', payload: response });
        })
    },
  }
};

class App extends Component {
  viewNote(id) {
    console.log("View", id);
  }
  deleteNote(id) {
    console.log("Delete", id);
  }

  constructor(props) {
    super(props);

    this.viewNote = this.viewNote.bind(this);
    this.deleteNote = this.deleteNote.bind(this);
  }

  render() {
    return (
      <div className="App">
        {this.props.notes.map(({ noteId, keywords, date, text }) => <Note
          noteId={noteId}
          keywords={keywords}
          date={date}
          text={text}
          viewNote={this.viewNote}
          deleteNote={this.deleteNote}
          key={noteId}
        />)}
        <button onClick={() => this.props.viewAllNotes()}>Load Notes</button>
      </div>
    );
  }
};
export default connect(mapNoteStateToProps, mapDispatchToProps)(App);


