import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import { Provider } from "react-redux";

import logger from "redux-logger";
import thunk from "redux-thunk";
import promise from "redux-promise-middleware";

import registerServiceWorker from './registerServiceWorker';
import { createStore, applyMiddleware } from 'redux';


let initialStoreState = {
  notes: [{
    noteId: 0,
    keywords: "A man, a fan, lambs",
    date: "11/12/19",
    text: "This note is about a man, a fan, and several lambs.",
  }],
}

let reducer = function (state, action) {
  if (state === undefined) {
    console.log('State is undefined');
    return initialStoreState;
  }

  switch (action.type) {
    case 'view_notes_pending': {
      return {
        ...state,
      };
    }
    case 'view_notes_failed': {
      console.log("ERR", action.payload);
      return {
        ...state,
      };
    }
    case 'view_notes_fulfilled': {
      return {
        ...state,
        notes: [
          ...state.notes,
          ...action.payload,
        ],
      }
    }
    default: {
      return {
        ...initialStoreState,
      };
    }
  }
  // https://redux.js.org/faq/react-redux#react-not-rerendering

}

const middleware = applyMiddleware(promise(), thunk, logger);

let store = createStore(reducer, initialStoreState, middleware);

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>
  ,
  document.getElementById('root'));
registerServiceWorker();



